<template>
  <div class="history-page">
    <div class="container">
      <el-card shadow="never">
        <template #header>
          <div class="page-header">
            <h2 class="header-font">历史记录</h2>
            <el-alert
              v-if="!authStore.isAuthenticated"
              class="tip-alert"
              title="登录后可查看您的历史记录"
              type="warning"
              :closable="false"
              show-icon
            >
              下方为示例记录，<el-link type="primary" @click="$router.push('/login')">登录</el-link>
              后即可查看与管理您的批改历史
            </el-alert>
            <el-alert
              v-else-if="!authStore.isMember"
              class="tip-alert"
              title="提示"
              type="warning"
              :closable="false"
              show-icon
            >
              非会员用户仅保存30天记录，<el-link type="primary" @click="$router.push('/profile')">
                升级会员
              </el-link>
              可永久保存
            </el-alert>
          </div>
        </template>

        <el-table
          v-loading="authStore.isAuthenticated && essayStore.loading"
          :data="displayList"
          class="history-table"
          @row-click="handleRowClick"
        >
          <el-table-column prop="title" label="标题" min-width="120">
            <template #default="{ row }">
              {{ row.title || '无标题' }}
            </template>
          </el-table-column>
          <el-table-column prop="word_count" label="字数" min-width="70" />
          <el-table-column prop="status" label="状态" min-width="90">
            <template #default="{ row }">
              <el-tag :type="getStatusType(row.status)">{{ getStatusText(row.status) }}</el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="created_at" label="提交时间" min-width="150">
            <template #default="{ row }">
              {{ new Date(row.created_at).toLocaleString() }}
            </template>
          </el-table-column>
          <el-table-column label="操作" min-width="220" align="right">
            <template #default="{ row }">
              <div class="op-row">
                <template v-if="authStore.isAuthenticated && row.id !== 0">
                  <el-button type="primary" text @click.stop="handleView(row)">查看</el-button>
                  <template v-if="row.recordType === 'essay'">
                    <el-button
                      v-if="!row.is_favorited"
                      type="success"
                      text
                      :loading="favoriteLoadingId === row.id"
                      @click.stop="handleFavorite(row)"
                    >
                      收藏
                    </el-button>
                    <el-button
                      v-else
                      type="warning"
                      text
                      :loading="unfavoriteLoadingId === row.id"
                      @click.stop="handleUnfavorite(row)"
                    >
                      取消收藏
                    </el-button>
                  </template>
                  <el-button type="danger" text @click.stop="handleDelete(row)">删除</el-button>
                </template>
                <template v-else>
                  <el-button type="primary" text disabled>查看</el-button>
                  <el-button type="success" text disabled>收藏</el-button>
                  <el-button type="danger" text disabled>删除</el-button>
                </template>
              </div>
            </template>
          </el-table-column>
        </el-table>

        <div
          v-if="authStore.isAuthenticated && displayList.length === 0 && !essayStore.loading"
          class="empty-state"
        >
          <el-empty description="暂无历史记录">
            <el-button type="primary" @click="$router.push('/essay/correction')">
              开始批改
            </el-button>
          </el-empty>
        </div>
      </el-card>
    </div>
  </div>
</template>

<script setup lang="ts">
import { computed, ref, onMounted } from 'vue'
import { useRouter } from 'vue-router'
import { useAuthStore } from '@/stores/auth'
import { useEssayStore } from '@/stores/essay'
import { unfavoriteEssay } from '@/api/essay'
import { ElMessage, ElMessageBox } from 'element-plus'

/** 历史列表项：批改记录或草稿，合并展示 */
interface HistoryListItem {
  id: number
  title?: string
  word_count: number
  status: string
  created_at: string
  updated_at: string
  is_favorited?: boolean
  recordType: 'essay' | 'draft'
}

const router = useRouter()
const authStore = useAuthStore()
const essayStore = useEssayStore()

// 未登录时展示的示例记录
const EXAMPLE_RECORD: HistoryListItem = {
  id: 0,
  title: '示例：我的暑假生活',
  word_count: 520,
  status: 'done',
  created_at: new Date().toISOString(),
  updated_at: new Date().toISOString(),
  is_favorited: false,
  recordType: 'essay',
}

const displayList = computed<HistoryListItem[]>(() => {
  if (!authStore.isAuthenticated) return [EXAMPLE_RECORD]
  const essays: HistoryListItem[] = essayStore.history.map((e) => ({
    id: e.id,
    title: e.title,
    word_count: e.word_count ?? 0,
    status: e.status,
    created_at: e.created_at,
    updated_at: e.updated_at,
    is_favorited: e.is_favorited,
    recordType: 'essay' as const,
  }))
  const drafts: HistoryListItem[] = essayStore.drafts.map((d) => ({
    id: d.id,
    title: d.title,
    word_count: d.content_text?.length ?? 0,
    status: 'draft',
    created_at: d.created_at,
    updated_at: d.updated_at,
    is_favorited: false,
    recordType: 'draft' as const,
  }))
  const merged = [...essays, ...drafts]
  merged.sort((a, b) => new Date(b.updated_at).getTime() - new Date(a.updated_at).getTime())
  return merged
})

const favoriteLoadingId = ref<number | null>(null)
const unfavoriteLoadingId = ref<number | null>(null)

onMounted(async () => {
  if (authStore.isAuthenticated) {
    await essayStore.fetchHistory()
    await essayStore.fetchDrafts()
  }
})

function getStatusType(status: string) {
  const map: Record<string, string> = {
    pending: 'info',
    processing: 'warning',
    done: 'success',
    failed: 'danger',
    draft: 'info',
  }
  return map[status] || 'info'
}

function getStatusText(status: string) {
  const map: Record<string, string> = {
    pending: '待处理',
    processing: '处理中',
    done: '已完成',
    failed: '失败',
    draft: '草稿',
  }
  return map[status] || status
}

function handleRowClick(row: HistoryListItem) {
  if (!authStore.isAuthenticated || row.id === 0) return
  if (row.recordType === 'draft') {
    router.push({ path: '/essay/correction', query: { draftId: String(row.id) } })
  } else {
    router.push(`/essay/${row.id}`)
  }
}

function handleView(row: HistoryListItem) {
  if (row.recordType === 'draft') {
    router.push({ path: '/essay/correction', query: { draftId: String(row.id) } })
  } else {
    router.push(`/essay/${row.id}`)
  }
}

async function handleFavorite(row: HistoryListItem) {
  if (row.recordType === 'draft') return
  favoriteLoadingId.value = row.id
  try {
    await essayStore.favoriteEssay(row.id)
    essayStore.setHistoryItemFavorited(row.id, true)
    ElMessage.success('收藏成功')
    essayStore.fetchHistory().catch(() => {})
  } catch (error: unknown) {
    ElMessage.error((error as Error).message || '收藏失败')
  } finally {
    favoriteLoadingId.value = null
  }
}

async function handleUnfavorite(row: HistoryListItem) {
  if (row.recordType === 'draft') return
  unfavoriteLoadingId.value = row.id
  try {
    await unfavoriteEssay(row.id)
    essayStore.setHistoryItemFavorited(row.id, false)
    ElMessage.success('已取消收藏')
    essayStore.fetchHistory().catch(() => {})
  } catch (error: unknown) {
    ElMessage.error((error as Error).message || '取消收藏失败')
  } finally {
    unfavoriteLoadingId.value = null
  }
}

async function handleDelete(row: HistoryListItem) {
  try {
    await ElMessageBox.confirm(
      row.recordType === 'draft' ? '确定要删除这份草稿吗？' : '确定要删除这条记录吗？',
      '提示',
      { type: 'warning' }
    )
    if (row.recordType === 'draft') {
      await essayStore.deleteDraft(row.id)
      ElMessage.success('草稿已删除')
    } else {
      await essayStore.deleteEssay(row.id)
      ElMessage.success('删除成功')
    }
  } catch (error: unknown) {
    if (error !== 'cancel') {
      ElMessage.error((error as Error).message || '删除失败')
    }
  }
}
</script>

<style scoped>
.history-page {
  min-height: calc(100vh - var(--header-height) - 80px);
  padding: var(--spacing-lg) 0;
  background: transparent;
}

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 var(--spacing-md);
}

.page-header {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-sm);
}

.page-header h2 {
  margin: 0;
  font-size: var(--font-size-xl);
  font-weight: 600;
}

.empty-state {
  padding: var(--spacing-xl) 0;
}

.history-table {
  width: 100%;
}

/* 空状态不显示表格内置的「暂无数据」行，由下方自定义 empty-state 展示 */
.history-table :deep(.el-table__empty-block) {
  display: none;
}

.op-row {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: flex-end;
  gap: var(--spacing-xs, 8px);
  min-width: 0;
}

.op-row .el-button {
  flex-shrink: 0;
}
</style>
